import { useTranslation } from "react-i18next";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../Ui/Border/BorderBottom";
import CreateButton from "../../Ui/Button/CreateButton";
import Modal from "../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../UserStaffMember/FinanceManager/Income/Income.module.css";
import QvipleLoader from "../../Loader/QvipleLoader";
import { useEffect, useState } from "react";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import AssignStaffModal from "../../../Department/Institute/AssignStaff/AssignStaffModal";
import { useFinanceUpdateModule } from "../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
const UpdateInstituteFinanceManager = ({ onClose, financeHead }) => {
  const { t } = useTranslation();
  const [state, setState] = useState("");
  const [assignHead, setAssignHead] = useState(false);
  const [disabled, setDisabled] = useState(false);

  const [financeUpdateModule] = useFinanceUpdateModule();

  useEffect(() => {
    setState(financeHead);
  }, [financeHead]);
  const onSelect = (val) => {
    setState(val);
  };
  const onUpdate = () => {
    if (financeHead?._id && state?._id) {
      setDisabled(true);
      financeUpdateModule({
        osid: financeHead?._id,
        nsid: state?._id,
      }).then(() => {
        setDisabled(false);
        onClose();
      });
    }
  };
  //   console.info("dsfhjbsdfks", financeHead, state);

  return (
    <>
      {assignHead ? (
        <AssignStaffModal
          assignHead={assignHead}
          headSelect={onSelect}
          title={t("secect_finance_head")}
          onClose={() => setAssignHead((pre) => !pre)}
          disabledId={state?._id}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("update_finance_manager_head")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <div
                className={style.card_container_image}
                onClick={() => setAssignHead(true)}
                style={{
                  cursor: "pointer",
                }}
              >
                <img
                  alt="not found"
                  src={
                    state?.staffProfilePhoto
                      ? `${imageShowUrl}/${state.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                />
                <div className={style.card_container_inner}>
                  <h6 className={style.namee}>{`${state?.staffFirstName ?? ""} 
                    ${state?.staffMiddleName ?? ""}
                    ${state?.staffLastName ?? ""} `}</h6>
                  <p className={style.desig}>
                    {t("index_number_1")} {state?.staffROLLNO ?? ""}
                  </p>
                </div>
              </div>
              <CreateButton label="update" onAction={onUpdate} />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default UpdateInstituteFinanceManager;
